import { Modal, Form, Input, Button, Space } from "antd";
import { useRef } from "react";
function AddDialog({
  title,
  visible,
  disabled,
  handleOk,
  handleCancel,
  ...props
}) {
  const formRef = useRef(null);
  const handleFinish = (params) => {
    handleOk(params).then((flag) => {
      if (flag) {
        formRef.current.resetFields();
      }
    });
  };
  const layout = {
    labelCol: {
      span: 4,
    },
    wrapperCol: {
      span: 16,
    },
  };
  const tailLayout = {
    wrapperCol: {
      offset: 4,
      span: 16,
    },
  };
  return (
    <Modal
      title={title}
      visible={visible}
      okText=""
      cancelText=""
      onCancel={handleCancel}
      {...props}
      footer={null}
    >
      <Form ref={formRef} name="addDialog" {...layout} onFinish={handleFinish}>
        <Form.Item
          label=""
          name="username"
          rules={[
            {
              required: true,
              message: "",
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item label="" name="description">
          <Input />
        </Form.Item>
        <Form.Item label="" name="weight">
          <Input />
        </Form.Item>
        <Form.Item label="" name="height">
          <Input />
        </Form.Item>
        <Form.Item {...tailLayout}>
          <Space>
            <Button onClick={handleCancel}></Button>
            <Button type="primary" htmlType="submit" disabled={disabled}>
              
            </Button>
          </Space>
        </Form.Item>
      </Form>
    </Modal>
  );
}
export default AddDialog;
